import React from 'react';

const MotorbikeRentalSection = () => {
  const rentals = [
    {
      image:
        'https://f1e425bd6cd9ac6.cmccloud.com.vn/cms-tool/post/images/244/img_card.png?v=5',
      title: 'Thuê xe máy tại Đà Lạt',
      description: 'Chỉ từ 80K, giao xe tận nơi',
    },
    {
      image:
        'https://f1e425bd6cd9ac6.cmccloud.com.vn/cms-tool/post/images/248/img_card.png?v=4',
      title: 'Thuê xe máy tại Nha Trang',
      description: 'Chỉ từ 100K, giao xe tận nơi',
    },
    {
      image:
        'https://f1e425bd6cd9ac6.cmccloud.com.vn/cms-tool/post/images/245/img_card.png?v=4',
      title: 'Thuê xe máy tại Vũng Tàu',
      description: 'Chỉ từ 110K, giao xe tận nơi',
    },
    // Bạn có thể thêm nhiều mục khác nếu cần
  ];

  return (
    <section className="motorbike-rental-section mt-12 px-4 md:px-6">
      <h2 className="text-3xl font-extrabold text-center text-rose-600 mb-12">
        Thuê Xe Máy
      </h2>
      <div className="relative">
        <div className="flex overflow-x-auto md:grid md:grid-cols-3 md:gap-8 space-x-6 md:space-x-0 pb-4 hide-scroll-bar">
          {rentals.map((rental, index) => (
            <div
              key={index}
              className="flex-none w-72 md:w-auto bg-gradient-to-br from-green-400 to-blue-500 rounded-xl shadow-lg overflow-hidden transform hover:scale-105 transition duration-300 cursor-pointer"
            >
              <img
                src={rental.image}
                alt={rental.title}
                className="w-full h-64 object-cover opacity-90"
              />
              <div className="p-6">
                <h3 className="text-2xl font-bold text-white mb-2">
                  {rental.title}
                </h3>
                <p className="text-white">{rental.description}</p>
              </div>
            </div>
          ))}
        </div>

        {/* Left Gradient */}
        <div className="absolute left-0 top-0 h-full w-8  pointer-events-none md:hidden"></div>
        {/* Right Gradient */}
        <div className="absolute right-0 top-0 h-full w-8  pointer-events-none md:hidden"></div>
      </div>
    </section>
  );
};

export default MotorbikeRentalSection;
